.Jumpscare {
  width: 100%;
  height: 10000px;
  animation: epilepsy 1ms;
  animation-iteration-count: infinite;
}
@keyframes epilepsy {
  0% {
    filter: invert(0);
  }
  100% {
    filter: invert(1);
  }
}

