.Person {
  display: inline-flex;
  flex-direction: column;
  text-align: center;
}
.Avatar {
  width: 15vw;
  margin: 20px;
  cursor: pointer;
  -webkit-user-select: none;
  transition: transform 250ms ease-out;
}

.Avatar:hover {
  transform: scale(0.9);
  transform-origin: center;
}
